import React from "react";

import { graphql, Link, useStaticQuery } from "gatsby";
import { Helmet } from 'react-helmet';

import CoverImg from "../components/cover_img";
import Layout from "../components/layout_inner";

export default (props) => {
  const data = useStaticQuery(graphql`
    query {
      ideas: file(relativePath: { eq: "posts/ideas.jpg" }) {
        childImageSharp {
          fluid {
            ...GatsbyImageSharpFluid_withWebp_tracedSVG
          }
        }
      }
      expecting: file(relativePath: { eq: "posts/expecting.jpg" }) {
        childImageSharp {
          fluid {
            ...GatsbyImageSharpFluid_withWebp_tracedSVG
          }
        }
      }
      kids: file(relativePath: { eq: "posts/kids.jpg" }) {
        childImageSharp {
          fluid {
            ...GatsbyImageSharpFluid_withWebp_tracedSVG
          }
        }
      }
    }
  `);
  const posts = [{
    type: 'ideas',
    title: 'Идеи для фото',
  }, {
    type: 'expecting',
    title: 'На каком сроке лучше фотографироваться в ожидании чуда?',
  }, {
    type: 'kids',
    title: 'Детская фотосессия и ее хитрости',
  }];

  return (
    <Layout {...props}>
      <Helmet title="Фотограф Минск | Блог" defer={false} />
      <h1>
        Блог
      </h1>
      <p>&nbsp;</p>
      <div className="cards">
        {posts.map(post => (
        <Link key={post.type} to={`/blog/${post.type}/`} className="card">
            <span className="thumbnail">
              <CoverImg fluid={data[post.type].childImageSharp.fluid} />
            </span>
            <span className="title">{post.title}</span>
          </Link>
        ))}
      </div>
    </Layout>
  );
};
